<template>
  <v-navigation-drawer
    :value="show"
    temporary
    right
    absolute
    class="pa-4"
    width="400"
    @input="handleDrawerInput"
  >
    <!-- ЗАГОЛОВОК -->
    <div class="mb-3">
      <v-btn
        color="grey"
        fab
        x-small
        outlined
        class="sidebar-collapse-button"
        @click="$emit('close')"
      >
        <v-icon>keyboard_arrow_right</v-icon>
      </v-btn>
      <span class="title">
        {{ $t('common.filters') }}
      </span>
    </div>
    <!-- ЗАГОЛОВОК КОНЕЦ -->

    <!-- ФИЛЬТР ПО АККАУНТАМ -->
    <v-autocomplete
      ref="commentsFeedFiltersBarAccountsAutocomplete"
      attach
      :items="accounts.list"
      item-value="id"
      item-text="name"
      :value="filters.accounts"
      :label="$t('common.account')"
      multiple
      clearable
      outlined
      :dense="!filters.accounts.length"
      hide-details
      chips
      deletable-chips
      small-chips
      :loading="loading.accounts"
      @focus="handleAccountsFocus"
    />
    <!-- ФИЛЬТР ПО АККАУНТАМ КОНЕЦ -->

    <!-- <div class="my-2 text-small">
      {{ $t('common.or').toLowerCase() }}
    </div> -->

    <!-- ФИЛЬТР ПО ТЕГАМ -->
    <!-- <v-autocomplete
      :value="filters.tags"
      :items="currentLevelTags.all"
      :label="$t('common.tagsAccounts')"
      prepend-inner-icon="fas fa-tags"
      multiple
      clearable
      outlined
      :dense="!filters.tags.length"
      hide-details
      chips
      deletable-chips
      small-chips
      @change="selectTags"
    /> -->
    <!-- ФИЛЬТР ПО ТЕГАМ КОНЕЦ -->
  </v-navigation-drawer>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'CommentsFeedFiltersBar',

  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      scrollHandlerAdded: false
    };
  },

  computed: {
    ...mapGetters({
      filters: 'comments/filters',
      accounts: 'comments/accounts',
      loading: 'comments/loading',
      // currentLevelTags: 'tags/currentLevelTags',
    }),
  },

  beforeDestroy() {
    const { $el } = this.$refs.commentsFeedFiltersBarAccountsAutocomplete;
    
    if ($el && $el.querySelector('.v-menu__content.v-autocomplete__content')) {
      $el.querySelector('.v-menu__content.v-autocomplete__content').addEventListener('scroll', this.scrollHandler);
    }
  },

  methods: {
    handleAccountsFocus() {
      const { $el } = this.$refs.commentsFeedFiltersBarAccountsAutocomplete;
    
      if ($el && !this.scrollHandlerAdded) {
        this.scrollHandlerAdded = true;

        setTimeout(() => {
          $el.querySelector('.v-menu__content.v-autocomplete__content').addEventListener('scroll', this.scrollHandler);
        }, 1000);
      }
    },

    handleDrawerInput(value) {
      if (value === false) this.$emit('close');
    },

    // selectTags(tags) {
    //   this.$store.dispatch('comments/setFiltersTags', tags);
    // },

    scrollHandler(e) {
      if (this.accounts.meta.isLastPage) return;
      if (this.loading.accounts) return;

      const { scrollTop, scrollHeight, clientHeight } = e.target;
      const scrollPosition = scrollTop / (scrollHeight - clientHeight);

      if (Math.round(scrollPosition * 100) > 95) {
        this.$store.dispatch('comments/loadAccounts', true);
      }
    }
  }
};
</script>

<style scoped>
.text-small {
  font-size: 14px;
  color: #9E9E9E;
}
</style>
