<template>
  <div class="py-2 d-flex justify-end align-center">
    <!-- ДРОВЕР ФИЛЬТРОВ -->
    <filters-bar
      :show="showFilters"
      @close="showFilters = false"
    />
    <!-- ДРОВЕР ФИЛЬТРОВ КОНЕЦ -->

    <!-- ФИЛЬТРЫ -->
    <v-btn
      small
      dark
      :text="filtersBtnIsText"
      :color="filtersColor"
      class="mr-3"
      @click="showFilters = true"
    >
      <v-icon
        :size="18"
        class="mr-1"
      >
        filter_list
      </v-icon>
      {{ $t('common.filters') }}
    </v-btn>
    <!-- ФИЛЬТРЫ КОНЕЦ -->

    <!-- ДАТА ПИКЕР -->
    <date-range-picker
      :date-range="filters.dates"
      :locale-data="localeData"
      :ranges="ranges"
      auto-apply
      :control-container-class="'form-control'"
      opens="left"
      @select="changeDates"
    >
      <template #input="picker">
        <v-text-field
          :value="dateRangePickerValue(picker)"
          dense
          solo
          single-line
          prepend-inner-icon="fas fa-calendar"
          hide-details
          readonly
        />
      </template>
    </date-range-picker>
    <!-- ДАТА ПИКЕР КОНЕЦ -->
  </div>
</template>

<script>
import moment          from 'moment';
import DateRangePicker from 'vue2-daterange-picker';
import { mapGetters }  from 'vuex';

import FiltersBar      from './CommentsFeedFiltersBar.vue';

export default {
  name: 'CommentsFeedTopbar',

  components: {
    FiltersBar,
    DateRangePicker,
  },

  data() {
    return {
      showFilters: false,
    };
  },

  computed: {
    ...mapGetters({
      filters: 'comments/filters',
    }),

    filtersColor() {
      if (this.filters.accounts.length || this.filters.tags.length) {
        return 'red';
      }
      return 'primary';
    },

    filtersBtnIsText() {
      return !this.filters.accounts.length && !this.filters.tags.length;
    },

    ranges() {
      const today = new Date();
      today.setHours(0, 0, 0, 0);

      const yesterday = new Date();
      yesterday.setDate(today.getDate() - 1);
      yesterday.setHours(0, 0, 0, 0);

      const daysAgo7 = new Date();
      daysAgo7.setDate(today.getDate() - 7);
      daysAgo7.setHours(0, 0, 0, 0);

      const daysAgo14 = new Date();
      daysAgo14.setDate(today.getDate() - 14);
      daysAgo14.setHours(0, 0, 0, 0);

      const daysAgo30 = new Date();
      daysAgo30.setDate(today.getDate() - 30);
      daysAgo30.setHours(0, 0, 0, 0);

      return {
        [this.$t('common.datePicker.ranges.today')]: [today, today],
        [this.$t('common.datePicker.ranges.yesterday')]: [yesterday, yesterday],
        [this.$t('common.datePicker.ranges.last7Days')]: [daysAgo7, today],
        [this.$t('common.datePicker.ranges.last14Days')]: [daysAgo14, today],
        [this.$t('common.datePicker.ranges.last30Days')]: [daysAgo30, today],
        [this.$t('common.datePicker.ranges.thisWeek')]: [moment().startOf('week').toDate(), today],
        [this.$t('common.datePicker.ranges.lastWeek')]: [moment().subtract(1, 'weeks').startOf('week').toDate(), moment().subtract(1, 'weeks').endOf('week').toDate()],
        [this.$t('common.datePicker.ranges.thisMonth')]: [new Date(today.getFullYear(), today.getMonth(), 1), today],
        [this.$t('common.datePicker.ranges.lastMonth')]: [new Date(today.getFullYear(), today.getMonth() - 1, 1), new Date(today.getFullYear(), today.getMonth(), 0)],
        [this.$t('common.datePicker.ranges.thisYear')]: [new Date(today.getFullYear(), 0, 1), new Date(today.getFullYear(), 11, 31)],
      };
    },

    localeData() {
      return {
        firstDay: localStorage.getItem('language') === 'ru' ? 1 : 0,
        direction: 'ltr',
        format: 'yyyy-mm-dd',
        separator: ' - ',
        applyLabel: 'Apply',
        cancelLabel: 'Cancel',
        weekLabel: 'W',
        customRangeLabel: 'Custom Range',
        daysOfWeek: [
          this.$t('common.datePicker.daysOfWeek.sun'),
          this.$t('common.datePicker.daysOfWeek.mon'),
          this.$t('common.datePicker.daysOfWeek.tue'),
          this.$t('common.datePicker.daysOfWeek.wed'),
          this.$t('common.datePicker.daysOfWeek.thu'),
          this.$t('common.datePicker.daysOfWeek.fri'),
          this.$t('common.datePicker.daysOfWeek.sat'),
        ],
        monthNames: [
          this.$t('common.datePicker.monthNames.jan'),
          this.$t('common.datePicker.monthNames.feb'),
          this.$t('common.datePicker.monthNames.mar'),
          this.$t('common.datePicker.monthNames.apr'),
          this.$t('common.datePicker.monthNames.may'),
          this.$t('common.datePicker.monthNames.jun'),
          this.$t('common.datePicker.monthNames.jul'),
          this.$t('common.datePicker.monthNames.aug'),
          this.$t('common.datePicker.monthNames.sep'),
          this.$t('common.datePicker.monthNames.oct'),
          this.$t('common.datePicker.monthNames.nov'),
          this.$t('common.datePicker.monthNames.dec'),
        ]
      };
    },
  },

  methods: {
    dateRangePickerValue(picker) {
      return `${moment(picker.startDate).format('YYYY-MM-DD')}-${moment(picker.endDate).format('YYYY-MM-DD')}`;
    },

    async changeDates({ startDate, endDate }) {
      startDate = this.moment(startDate).format('YYYY-MM-DD');
      endDate = this.moment(endDate).format('YYYY-MM-DD');

      localStorage.setItem('comments-filters-dates', JSON.stringify({ startDate, endDate }));
      await this.$store.dispatch('comments/setFiltersDates', { startDate, endDate });

      this.$store.dispatch('comments/loadFbComments');
    }
  }
};
</script>
