<template>
  <div>
    <!-- ТАБЫ -->
    <v-progress-linear :indeterminate="mainLoading" />
    <tabs />
    <!-- ТАБЫ КОНЕЦ -->

    <!-- КОНТЕНТ -->
    <component :is="componentName" />
    <!-- КОНТЕНТ КОНЕЦ -->
  </div>
</template>

<script>
import Tabs           from '@/components/comments/CommentsTabs.vue';
import Feed           from '@/views/comments/CommentsFeed.vue';
import Settings       from '@/views/comments/CommentsSettings.vue';
import { mapGetters } from 'vuex';

export default {
  name: 'Comments',

  components: {
    Tabs,
    Feed,
    Settings
  },

  computed: {
    ...mapGetters({
      loading: 'comments/loading',
    }),

    componentName() {
      if (typeof this.$route.params.tab !== 'undefined') {
        return this.$route.params.tab;
      }
      return 'feed';
    },
    
    mainLoading() {
      return this.loading.settings || this.loading.fbComments;
    }
  },

  created() {
    this.$store.dispatch('comments/loadFbComments');
    this.$store.dispatch('comments/loadSettings');
    this.$store.dispatch('comments/loadAccounts');
    // this.$store.dispatch('tags/loadItems', {
    //   only_from: 'accounts',
    //   exclude_tags: 0
    // });
  }
};
</script>
