<template>
  <v-card outlined>
    <!-- ЗАГОЛОВОК -->
    <v-card-title>
      {{ $t('comments.mode') }}
    </v-card-title>
    <!-- ЗАГОЛОВОК КОНЕЦ-->

    <v-card-text>            
      <!-- РАБОТАТЬ С КОММЕНТАРИЯМИ -->
      <v-switch
        :input-value="settings.process"
        :label="$t('settings.facebookApi.dataUpdate.comments')"
        dense
        hide-details
        class="mt-0 pt-0"
        @change="handleProccessChange"
      />
      <!-- РАБОТАТЬ С КОММЕНТАРИЯМИ КОНЕЦ -->

      <!-- ИНТЕРВАЛ РАБОТЫ С КОММЕНТАРИЯМИ -->
      <v-text-field
        v-if="settings.process"
        :value="settings.interval"
        :label="$t('settings.exportCosts.interval')"
        class="mt-3 comments-settings-interval-input"
        outlined
        dense
        hide-details
        :suffix="$t('common.min').toLowerCase()"
        @input="handleIntervalInput"
      />
      <!-- ИНТЕРВАЛ РАБОТЫ С КОММЕНТАРИЯМИ КОНЕЦ -->

      <!-- РЕЖИМ РАБОТЫ С КОММЕНТАРИЯМИ -->
      <v-select
        v-if="settings.process"
        :value="settings.mode"
        :items="commentsModes"
        outlined
        :label="$t('settings.facebookApi.comments.mode')"
        dense
        hide-details
        class="mt-3 comments-settings-interval-input"
        @change="handleModeChange"
      />
      <!-- РЕЖИМ РАБОТЫ С КОММЕНТАРИЯМИ КОНЕЦ -->
    </v-card-text>

    <!-- ДЕЙСТВИЯ -->
    <v-card-actions>
      <v-btn
        text
        color="primary"
        :loading="loading"
        @click="save"
      >
        {{ $t('common.save') }}
      </v-btn>
    </v-card-actions>
    <!-- ДЕЙСТВИЯ КОНЕЦ -->
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'CommentsSettingsMode',
  
  data() {
    return {
      loading: false,
      commentsModes: [
        {text: this.$t('settings.facebookApi.comments.download'), value: 'download'},
        {text: this.$t('settings.facebookApi.comments.hide_all'), value: 'hide_all'},
        {text: this.$t('settings.facebookApi.comments.hide_new'), value: 'hide_new'},
        {text: this.$t('settings.facebookApi.comments.delete_all'), value: 'delete_all'},
        {text: this.$t('settings.facebookApi.comments.delete_new'), value: 'delete_new'},
      ],
    };
  },

  computed: {
    ...mapGetters({
      settings: 'comments/settings',
    }),
  },

  methods: {
    handleProccessChange(value) {
      this.$store.dispatch('comments/setSettingsProperty', { key: 'process', value });
    },

    handleIntervalInput(value) {
      this.$store.dispatch('comments/setSettingsProperty', { key: 'interval', value });
    },

    handleModeChange(value) {
      this.$store.dispatch('comments/setSettingsProperty', { key: 'mode', value });
    },
 
    async save() {
      try {
        this.loading = true;

        await this.api.put('/fb-comment-settings', {
          process: this.settings.process,
          mode: this.settings.mode,
          interval: this.settings.interval,
        });
      } catch (error) {
        // 
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
<style>
.comments-settings-interval-input {
  max-width: 200px;
}
</style>