<template>
  <v-timeline
    ref="fbTimeline"
    dense
    class="comments-fb-timeline"
    :style="{height: `${cardHeight}px`}"
  >
    <template v-if="fbComments.length">
      <timeline-item
        v-for="(comment, commentIndex) in fbComments"
        :key="`comments-feed-fb-comment-${commentIndex}-${comment}`"
        :comment="comment"
        :comment-index="commentIndex"
      />
    </template>

    <v-timeline-item
      v-else
      hide-dot
    >
      {{ $t('adsmanager.ads.noComments') }}
    </v-timeline-item>
  </v-timeline>
</template>

<script>
import { mapGetters } from 'vuex';

import TimelineItem   from './CommentsFeedTimelineItem.vue';

export default {
  name: 'CommentsFeedFbTimeline',

  components: {
    TimelineItem
  },

  computed: {
    ...mapGetters({
      innerHeight: 'main/innerHeight',
      fbComments: 'comments/fbComments',
      meta: 'comments/meta',
      loading: 'comments/loading',
    }),

    cardHeight() {
      return this.innerHeight - 4 - 32 - 46 - 16 - 4 - 24 - 64 - 24;
    }
  },

  mounted() {
    this.$refs.fbTimeline.$el.addEventListener('scroll', this.fbScrollHandler);
  },

  beforeDestroy() {
    this.$refs.fbTimeline.$el.removeEventListener('scroll', this.fbScrollHandler);
  },

  methods: {
    async fbScrollHandler(e) {
      if (this.meta.isLastPage) return;
      if (this.loading.fbComments) return;

      const { scrollTop, scrollHeight, clientHeight } = e.target;
      const scrollPosition = scrollTop / (scrollHeight - clientHeight);

      if (Math.round(scrollPosition * 100) > 95) {
        this.$store.dispatch('comments/loadFbComments', true);
      }
    },

  }
};
</script>

<style>
.comments-fb-timeline {
  overflow-y: auto;
}
</style>