<template>
  <v-timeline-item
    small
    class="mr-4"
    :color="commentColor"
  >
    <v-card>
      <!-- ЗАГОЛОВОК -->
      <v-card-title class="d-flex justify-space-between align-center">
        <!-- ЛЕВАЯ ЧАСТЬ -->
        <div class="d-flex align-center">
          <!-- СТАТУСЫ КОММЕНТАРИЯ -->
          <v-icon
            v-if="statusIcon"
            size="15"
            color="red"
            class="mr-2"
          >
            {{ statusIcon }}
          </v-icon>
          <!-- СТАТУСЫ КОММЕНТАРИЯ КОНЕЦ -->

          <!-- АВТОР КОММЕНТАРИЯ -->
          <span
            v-if="comment.author"
            :class="{'hidden-or-deleted': commentIsHiddenOrDeleted}"
          >
            {{ comment.author }}
          </span>
          <!-- АВТОР КОММЕНТАРИЯ КОНЕЦ -->
        </div>
        <!-- ЛЕВАЯ ЧАСТЬ КОНЕЦ -->

        <!-- ПРАВАЯ ЧАСТЬ -->
        <div class="d-flex align-center">
          <!-- ДЕЙСТВИЯ -->

          <!-- СКРЫТЬ -->
          <v-btn 
            v-if="comment.status === 'new'"
            v-bind="actionBtnProps"
            :loading="loading.hide"
            @click="hideComment"
          >
            {{ $t('common.hide').toLowerCase() }}
          </v-btn>
          <!-- СКРЫТЬ КОНЕЦ -->

          <!-- ПОКАЗАТЬ -->
          <v-btn 
            v-if="comment.status === 'hidden'"
            v-bind="actionBtnProps"
            :loading="loading.show"
            @click="showComment"
          >
            {{ $t('common.show').toLowerCase() }}
          </v-btn>
          <!-- ПОКАЗАТЬ КОНЕЦ -->

          <!-- УДАЛИТЬ -->
          <v-btn 
            v-if="commentIsHiddenOrNew"
            v-bind="actionBtnProps"
            :loading="loading.delete"
            @click="deleteComment"
          >
            {{ $t('common.delete').toLowerCase() }}
          </v-btn>
          <!-- УДАЛИТЬ КОНЕЦ -->
          <!-- ДЕЙСТВИЯ КОНЕЦ -->
            
          <!-- ДАТА ДОБАВЛЕНИЯ -->
          <span class="text-small-grey ml-2">
            {{ moment().format('YYYY-MM-DD HH:mm') }}
          </span>
          <!-- ДАТА ДОБАВЛЕНИЯ КОНЕЦ -->
        </div>
        <!-- ПРАВАЯ ЧАСТЬ КОНЕЦ-->
      </v-card-title>
      <!-- ЗАГОЛОВОК КОНЕЦ -->

      <!-- ТЕКСТ КОММЕНАТАРИЯ -->
      <v-card-text>
        <!-- ПРЕДУПРЕЖДЕНИЕ, ЧТО КОММЕНТ БЫЛ СКРЫТ ВРУЧНУЮ -->
        <div
          v-if="showUnhiddenManuallyWarning"
          class="mb-4 text-small-grey"
        >
          {{ $t('comments.unhiddenManuallyWarning') }}
        </div>
        <!-- ПРЕДУПРЕЖДЕНИЕ, ЧТО КОММЕНТ БЫЛ СКРЫТ ВРУЧНУЮ КОНЕЦ -->

        <!-- ТЕКСТ КОММЕНТАРИЯ -->
        <span :class="{'hidden-or-deleted': commentIsHiddenOrDeleted}">
          {{ comment.message }}
        </span>
        <!-- ТЕКСТ КОММЕНТАРИЯ КОНЕЦ -->

        <!-- НАЗВАНИЕ АККАУНТА -->
        <div
          v-if="comment.ad_account"
          class="text-small-grey"
        >
          {{ `${$t('common.adAccount')}: ${comment.ad_account.name}` }}
        </div>
        <!-- НАЗВАНИЕ АККАУНТА КОНЕЦ -->
      </v-card-text>
      <!-- ТЕКСТ КОММЕНАТАРИЯ КОНЕЦ -->
    </v-card>
  </v-timeline-item>
</template>
<script>
export default {
  name: 'CommentsFeedTimelineItem',

  props: {
    comment: {
      type: Object,
      required: true
    },
    commentIndex: {
      type: Number,
      required: true
    },
  },

  data() {
    return {
      actionBtnProps: {
        text: true,
        xSmall: true,
        color: 'primary'
      },
      loading: {
        show: false,
        hide: false,
        delete: false,
      },
    };
  },

  computed: {
    commentColor() {
      switch (this.comment.status) {
      case 'new':
        return 'primary';
      default:
        return 'grey';
      }
    },

    statusIcon() {
      switch (this.comment.status) {
      case 'hidden':
        return 'far fa-eye-slash';
      case 'deleted':
        return 'fas fa-trash-alt';
      default:
        return null;
      }
    },

    commentIsHiddenOrDeleted() {
      return this.comment.status === 'hidden' || this.comment.status === 'deleted';
    },

    commentIsHiddenOrNew() {
      return this.comment.status === 'hidden' || this.comment.status === 'new';
    },

    actionName() {
      switch (this.comment.status) {
      case 'new':
        return this.$t('common.hide').toLowerCase();
      case 'hidden':
        return this.$t('common.show').toLowerCase();
      default:
        return null;
      }
    },

    showUnhiddenManuallyWarning () {
      return this.comment.unhidden_from_dolphin && this.comment.status === 'new';
    }
  },

  methods: {
    async hideComment() {
      this.loading.hide = true;

      const comment = this.$_.cloneDeep(this.comment);
      comment.status = 'hidden';

      try {
        await this.api.patch('/fb-comments', { action: 'hide', ids: [comment.id] });
      } catch (error) {
        // 
      }
      
      this.$store.dispatch('comments/replaceFbComment', {
        index: this.commentIndex,
        comment,
      });

      this.loading.hide = false;
    },

    async showComment() {
      this.loading.show = true;

      const comment = this.$_.cloneDeep(this.comment);
      comment.status = 'new';
      comment.unhidden_from_dolphin = true;

      try {
        await this.api.patch('/fb-comments', { action: 'unhide', ids: [comment.id] });
      } catch (error) {
        // 
      }
      
      this.$store.dispatch('comments/replaceFbComment', {
        index: this.commentIndex,
        comment,
      });

      this.loading.show = false;
    },

    async deleteComment() {
      this.loading.delete = true;

      const comment = this.$_.cloneDeep(this.comment);
      comment.status = 'deleted';
      
      try {
        await this.api.delete('/fb-comments', { ids: [comment.id] });
      } catch (error) {
        // 
      }

      this.$store.dispatch('comments/replaceFbComment', {
        index: this.commentIndex,
        comment,
      });

      this.loading.delete = false;
    },
  }
};
</script>
<style scoped>
.hidden-or-deleted {
  color: #9E9E9E;
  text-decoration: auto; 
}
.text-small-grey {
  color: #9E9E9E;
  font-size: 12px;
}
</style>