<template>
  <v-tabs
    centered
    grow
    :value="tabNumber"
    :height="32"
  >
    <!-- ЛЕНТА КОММЕНТАРИЕВ -->
    <v-tab
      key="tab-feed"
      :value="0"
      @click="selectTab('feed')"
    >
      {{ $t('comments.tabs.commentsFeed') }}
    </v-tab>
    <!-- ЛЕНТА КОММЕНТАРИЕВ КОНЕЦ -->

    <!-- НАСТРОЙКИ -->
    <v-tab 
      key="tab-settings"
      :value="1"
      @click="selectTab('settings')"
    >
      {{ $t('comments.tabs.settings') }}
    </v-tab>
    <!-- НАСТРОЙКИ КОНЕЦ -->
  </v-tabs>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'CommentsTabs',

  data() {
    return {
      tabs: ['feed', 'settings']
    };
  },

  computed: {
    ...mapGetters({
      activeTab: 'comments/activeTab',
    }),

    tabNumber() {
      switch (this.$route.params.tab) {
      case 'feed':
        return 0;
      case 'settings':
        return 1;
      default:
        return 0;
      }
    }
  },

  methods: {
    changeTab(tab) {
      this.$store.dispatch('comments/setActiveTab', {
        tab: tab,
        component: this.tabs[tab]
      });
    },

    selectTab(tab) {
      this.$router.push({
        path: `/comments/${tab}`,
        params: { tab }
      });
    }
  }
};
</script>